.customizer{width:400px;right:-400px;padding:0;background-color:#fff;z-index:1051;position:fixed;top:0;bottom:0;height:100vh;transition:right 0.4s cubic-bezier(0.05, 0.74, 0.2, 0.99);backface-visibility:hidden;border-left:1px solid rgba(0,0,0,0.05);box-shadow:0 15px 30px 0 rgba(0,0,0,0.11),0 5px 15px 0 rgba(0,0,0,0.08)}.customizer .open-icon{animation:Spin 2s infinite linear;position:relative;top:-2px}.customizer.open{right:0}.customizer .customizer-content{position:relative;height:calc(100% - 7rem)}.customizer .customizer-content .bg-default{background-color:#fff}.customizer .customizer-close{position:absolute;right:30px;top:20px;padding:7px;width:auto;z-index:10;color:#626262}.customizer .customizer-close i{font-size:1.71rem}.customizer .customizer-toggle{background:#7367f0;color:#fff;display:block;box-shadow:-3px 0px 8px rgba(0,0,0,0.1);border-top-left-radius:6px;border-bottom-left-radius:6px;position:absolute;top:50%;width:38px;height:38px;left:-39px;text-align:center;line-height:40px;cursor:pointer}.customizer .color-box{height:35px;width:35px;margin:0.5rem;border-radius:0.5rem;cursor:pointer}.customizer .color-box.selected{box-shadow:0 0 0 3px rgba(52,144,220,0.5);transition:all 0.2s ease-in-out}.customizer .color-box.disabled{pointer-events:none;cursor:not-allowed}body.dark-layout .hide-scroll-top-switch .custom-switch .custom-control-label:before,body.dark-layout .collapse-sidebar .custom-switch .custom-control-label:before{background-color:#10163a}.buy-now{position:fixed;bottom:5%;right:79px;z-index:1031}.buy-now .btn{box-shadow:0 1px 20px 1px #EA5455 !important}.buy-now .btn:hover{box-shadow:none !important}@keyframes Spin{from{transform:rotate(0)}to{transform:rotate(359deg)}}
